
h1 {
    font-size: 50px;
}
@media (max-width: 640px) {
h1 {
    font-size: 40px;
}
}


































.switch {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin: 40px 0 30px;
}
.switch__btn {
    margin-right: 10px;
}
.books-row {
    margin-right: -0.75rem;
    margin-left: -0.75rem;
}

.book {
    margin-bottom: 10px;
}
.book .book__title {
    margin-top: 0;
    font-weight: 500;
}
.book__description {
    font-size: 17px;
}
.book__author {
    margin: 0;
}
.book__year {
    margin: 0;
}
.book__delete {
    padding: 0;
    background-color: transparent;
    border: none;
    padding: 10px;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}
.book__delete:hover,
.book__delete:focus {
    background-color: transparent;
    opacity: 0.7;
}
.book__read-year {
    margin: 5px 0 0;
    font-size: 13px;
}

.add {
    margin-top: 40px;
}
.add input, .add textarea {
    font-size: 18px;
}
.add input[type=text]:focus:not([readonly]), .add textarea:focus:not([readonly]) {
    border-bottom: 1px solid #4527a0;
    -webkit-box-shadow: 0 1px 0 0 #4527a0;
            box-shadow: 0 1px 0 0 #4527a0;
}
.add__btn {
    display: block;
    margin: 40px auto 0;
}

.edit {
    margin-top: 40px;
}
.edit input, .edit textarea {
    font-size: 18px;
}
.edit input[type=text]:focus:not([readonly]), .edit textarea:focus:not([readonly]) {
    border-bottom: 1px solid #4527a0;
    -webkit-box-shadow: 0 1px 0 0 #4527a0;
            box-shadow: 0 1px 0 0 #4527a0;
}
.edit__btn {
    display: block;
    margin: 40px auto 0;
}

.login {
    margin-top: 40px;
}
.login input {
    font-size: 18px;
}
.login input[type=email]:focus:not([readonly]),
.login input[type=password]:focus:not([readonly]) {
    border-bottom: 1px solid #4527a0;
    -webkit-box-shadow: 0 1px 0 0 #4527a0;
            box-shadow: 0 1px 0 0 #4527a0;
}
.login__btn {
    display: block;
    margin: 40px auto 0;
}
